@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/********************************************/

nav {
  display: flex;
  justify-content: left;
  background-color: #000;
  padding: 11px;
}

nav ul li a {
  color: #fff;
  margin-inline: 11px;
}

.bg {
  min-height: 25vh;
  width: 100%;
  background-image: url("./images/BG_Image.jpg");
  background-size: cover;
  background-repeat: no-repeat;
}

.heading {
  font-size: 20px;
  padding: 10px 10px 10px 10px;
}

.emp_Table {
  background-color: #ededed;
  text-align: center;
  padding: 20px 20px 40px 20px;
  border-radius: 15px;
  position: relative;
  z-index: 0;
  margin-top: 20px;
  width: 80%;
}

.thead {
  background-color: #c1c1c1;
  font-weight: 500;
}

.inputs {
  display: flex;
  flex-direction: column;
  margin-bottom: 15px;
  border-radius: 3px;
  padding: 0px 10px 0px 10px;
  margin-top: 10px;
  gap: 0px;
}

.input {
  display: flex;
  align-items: center;
  margin: auto;
  width: 350px;
  height: 30px;
  background: #eaeaea;
  border-radius: 5px;
  padding: 5px 10px 5px 10px;
  font-size: 15px;
  border: 1px solid #bbbbbb;
}

.add_Btn {
  display: flex;
  justify-content: center;
  background: #297000;
  align-items: center;
  color: #fff;
  cursor: pointer;
  border-radius: 5px;
  width: 70px;
  height: 35px;
  border: none;
}

.reset_Btn {
  display: flex;
  justify-content: center;
  background: #464646;
  align-items: center;
  color: #fff;
  cursor: pointer;
  border-radius: 5px;
  width: 70px;
  height: 35px;
  border: none;
}

.addemp_Container {
  background-color: #ededed;
  text-align: center;
  padding: 20px 20px 40px 20px;
  border-radius: 15px;
  position: relative;
  z-index: 0;
  margin-top: 20px;
}

.reqField {
  margin-top: 0px;
    margin-bottom: 0px;
    margin-left: 18px;
    color: red;
    font-size: 12px;
    text-align: left;
}